/* ********************** Landing ***************************** */
.waveMarginBottomFix {
  margin-bottom: -2px;
}
.waveMarginTopFix {
  margin-top: -2px;
}
.landingTitle {
  font-weight: 700;
  font-size: 3.3rem;
  line-height: 3rem;
  color: #121212;
}
.landingPara {
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 1.7rem;
  color: #242424;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

@media (max-width: 1280px) {
  /* IPad Pro*/
  .landingTitle {
    padding-top: 1rem;
    font-weight: 700;
    font-size: 2.6rem;
    line-height: 2.6rem;
    color: #242424;
  }
  .landingPara {
    font-weight: 500;
    font-size: 1.2rem;
    line-height: 23px;
    color: #242424;
    padding-right: 1rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  .landingBtns {
    font-size: 0.1rem;
    margin-top: 2rem;
  }
}

.landingBtns {
  margin-top: 2rem;
  padding-left: 0.1rem;
}
.landingBtns button {
  border: 1px solid #242424;
  font-weight: 600;
  font-size: 1.2rem;
  outline: none;
  box-shadow: none;
}
.landingBtns button:focus {
  box-shadow: none;
}
.arrowRight {
  transition: 0.2s linear;
}
.hvr-icon:hover .arrowRight {
  transform: translateX(6px);
}
.belikeTitle {
  font-weight: 700;
  font-size: 3.3rem;
}
.belikePara {
  font-weight: 500;
  font-size: 1.6rem;
  opacity: 0.8;
}
@media screen and (max-width: 768px) {
  /* Mobile */
  .landingTitle {
    text-align: center;
    font-size: 2.3rem;
  }
  .landingPara {
    text-align: center;
    font-size: 1.2rem;
  }
  .landingRightDiv {
    padding: 1rem;
    margin-top: 4rem;
  }
  .landingBtns {
    margin-top: 2rem;
    margin-bottom: 3rem;
    text-align: center;
  }
  .belikeTitle {
    font-size: 2.3rem;
  }
  .belikePara {
    font-size: 1.1rem;
  }
}

/* ************* Landing Ends **************** */
/* ***************** Count ******************** */
.countBox {
  background-image: url(data:image/svg+xml;base64,);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 120px 0px 120px 0px;
}
.whyTitle {
  font-weight: 700;
  font-size: 2.9rem;
}
.footerText {
  color: #fff;
  font-weight: 400;
  opacity: 0.7;
}
