/*************** Testimonials ******************/
.alert {
  border: none;
}
.titleTestimonials {
  color: #121212;
  font-weight: 700;
  font-size: 2.6rem;
  opacity: 0.9;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.cardTestimonials {
  border-radius: 1.6rem;
  background: linear-gradient(90deg, #53125d, #321858 80%);
  color: #fff;
}
.cardNameTestimonials {
  font-size: 1.3rem;
  opacity: 0.9;
  font-weight: 600;
}
.cardCompanyTestimonials {
  font-size: 1rem;
  font-weight: 300;
  opacity: 0.6;
}
.cardReviewTestimonials {
  font-weight: 500;
  opacity: 0.75;
  font-size: 1rem;
}
